nav {background-color: #5f5f5f; /* Light gray background */
padding: 1rem;
display: flex;
justify-content: center; /* Centers the content horizontally */
}

nav ul {
list-style: none;
display: flex;
gap: 1.5rem; /* Space between menu items */
margin: 0;
padding: 0;
}

nav ul li a {
text-decoration: none;
color: blue;
font-weight: bold;
position: relative;
transition: color 0.3s ease; /* Smooth color transition */
}

nav ul li a::before {
content: '';
position: absolute;
width: 100%;
height: 2px;
bottom: -5px;
left: 0;
background-color: darkblue;
visibility: hidden;
transform: scaleX(0);
transition: all 0.3s ease-in-out;
}

nav ul li a:hover {
color: darkblue; /* Change color on hover for better interactivity */
}

nav ul li a:hover::before {
visibility: visible;
transform: scaleX(1);
}